@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap');

:root{
  --main-font: 'Inter', sans-serif;
  --red-color: #ec1839;
  --orange-color: #fa5b0f;
  --green-color: #37b182;
  --blue-color: #1854b4;
  --pink-color: #f021b2;
  --dark-bg: #1B2430;
  --dark-bg-alt: #181d24; 
  font-size: 14px;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  transition: background-color .25s;
}
html{
  scroll-behavior: smooth;
}
body {
  font-family: var(--main-font);
}
li{
  list-style: none;
}
h1, h2, h3, button{
  letter-spacing: 1px;
}
button, a{
  font-family: var(--main-font);
  text-decoration: none;
}
header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20;
  box-shadow: 1px 1px 3px rgba(0,0,0,.2);
}
header h1{
  text-transform: capitalize;
  cursor: pointer;
  font-size: 1.8em;
}
header ul{
  display: flex;
  align-items: center;
}
header ul li{
  margin-left: 9px;
}
header button{
  font-size: 1.2em;
  display: flex;
  align-items: center;
  color: white;
  padding: 6px;
  border-radius: 2px;
  border: 2px solid white;
  cursor: pointer;
  transition: .2s;
}
header button:hover{
  transform: scale(1.15);
  background-color: white !important;
}
header .languageBtn{
  font-size: 1em;
  font-weight: bold;
  text-transform: uppercase;
}
header .logoutBtn{
  border: none;
  font-size: .85em;
  font-weight: bold;
  padding: 8px 4px;
  position: relative;
}
header .logoutBtn::before{
  content: "";
  width: 1px;
  height: 20px;
  background-color: white;
  position: absolute;
  left: -2px;
}
header .logoutBtn:hover{
  transform: scale(1.05);
}
header .logoutBtn:hover::before{
  display: none;
}
header .logoutBtn svg{
  margin-left: 2px;
}
.searchBar{
  width: 100%;
  padding: 110px 0 50px;
}
.searchBar input{
  width: 500px;
  padding: 10px;
  border-radius: 4px;
  display: block;
  border: 1px solid black;
  margin: 0 auto;
  background-color: rgb(250, 250, 250);
  box-shadow: 2px 2px 2px rgba(0,0,0,.2);
}
.noteContainer{
  width: 1100px;
  border-top: 1px solid gray;
  padding-top: 20px;
  margin: 0 auto;
}
.noteListToggler button{
  background-color: rgb(250, 250, 250);
  border-radius: 4px;
  padding: 7px 15px;
  color: black;
  font-weight: bold;
  margin-right: 10px;
  cursor: pointer;
  transition: .25s;
  box-shadow: 2px 2px 2px rgba(0,0,0,.2);
}
.noteListToggler button:hover{
  transform: scale(1.05);
}
.noteListToggler button.active{
  color: white !important;
}
.noteList{
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
}
.noteList h2{
  text-align: center;
  text-decoration: underline;
  text-transform: uppercase;
  width: 100%;
  margin-bottom: 30px;
}
.noteItem{
  padding: 25px 15px;
  width: calc(25% - 15px);
  box-shadow: 3px 3px 3px rgba(0,0,0,.2);
  margin-right: 15px;
  margin-bottom: 25px;
  border-radius: 4px;
  height: 300px;
  position: relative;
  cursor: pointer;
  transition: .25s;
  background-color: rgb(250, 250, 250);
}
.noteItem h3{
  margin-bottom: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.noteItem .date{
  font-size: .9em;
  color: gray;
  margin-bottom: 15px;
}
.noteItem .body{
  line-height: 1.5em;
  letter-spacing: 1px;
  height: 170px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 8;
  -webkit-box-orient: vertical;
}
.noteItem button{
  background-color: rgb(250, 250, 250);
  border: none;
  position: absolute;
  padding: 10px 15px;
  bottom: 2px;
  right: 2px;
  font-size: .7em;
  font-family: var(--main-font);
  cursor: pointer;
  transition: .25s;
}
.noteItem:hover{
  background-color: rgb(240, 240, 240);
}
.noteItem:hover button{
  background-color: rgb(240, 240, 240);
  text-decoration: underline;
}
.noteItem a{
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
figure{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.emptyImage{
  display: block;
  width: 300px;
}
.emptyAttribution{
  margin-top: -12px;
  margin-bottom: 20px;
  color: gray;
}
.emptyAttribution a{
  color: gray;
  text-decoration: underline;
}
.emptyMessage{
  font-weight: bold;
  font-size: 1.2em;
  letter-spacing: 1px;
  margin-bottom: 40px;
}
footer{
  padding: 10px;
  box-shadow: -2px -2px 3px rgba(0,0,0,.2);
}
footer p{
  text-align: center;
  color: white;
}
footer p a{
  color: white;
  text-decoration: underline;
}
main{
  min-height: calc(100vh - 37px);
}
.loading{
  min-height: calc(100vh - 37px);
  display: flex;
  justify-content: center;
  align-items: center;
}
.notePage .loading{
  min-height: calc( 100vh - 286px);
}
.error{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.error p{
  text-align: center;
  width: 100%;
}
/* CRUD NOTE PAGE */
.CRUDNote{
  width: 1100px;
  margin-inline: auto;
  padding: 90px 15px 15px;
}
.CRUDNote h2{
  text-align: center;
  margin-bottom: 20px;
}
.CRUDNote form{
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.CRUDNote .remainingChar{
  width: 100%;
  text-align: end;
  padding: 0;
}
.CRUDNote label{
  width: 150px;
  margin: 5px 0;
  padding: 10px;
}
.CRUDNote input, .CRUDNote p, .CRUDNote textarea{
  width: calc(100% - 150px);
  margin: 5px 0;
  font-size: 1em;
}
.CRUDNote input, .CRUDNote textarea{
  padding: 10px;
  font-family: var(--main-font);
}
.CRUDNote p{
  padding: 10px 0;
}
.CRUDNote textarea{
  height: 40vh;
}
.CRUDNoteBtn{
  display: flex;
  position: fixed;
  bottom: 42px;
  right: 20px;
}
.CRUDNoteBtn button{
  font-size: 1.3em;
  display: flex;
  align-items: center;
  color: white;
  padding: 5px;
  border-radius: 2px;
  cursor: pointer;
  transition: .2s;
  margin-left: 9px;
  box-shadow: 2px 2px 3px rgba(0,0,0,.2);
}
.CRUDNoteBtn button:hover{
  transform: scale(1.15);
}
/* LOGIN REGISTER PAGE */
.LoginRegister{
  width: 400px;
  min-height: calc(100vh - 37px);
  margin-inline: auto;
  padding: 80px 15px 15px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.LoginRegister h2{
  width: 100%;
  text-align: center;
  margin-bottom: 15px;
}
.LoginRegister form{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.LoginRegister form label{
  padding: 10px;
  text-align: center;
  color: white;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  margin: 7px 0;
}
.LoginRegister form input{
  padding: 10px;
  width: calc(100% - 34px);
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
  outline: none;
  margin: 7px 0;
  background-color: whitesmoke;
}
.LoginRegister form button{
  border: none;
  padding: 7px 10px;
  border-radius: 3px;
  color: white;
  cursor: pointer;
  margin: 10px auto;
  transition: .2s;
}
.LoginRegister form button:hover{
  transform: scale(1.07);
}
.LoginRegister form p{
  margin: 7px 0;
  width: 100%;
}
.LoginRegister form p a{
  color: black;
  text-decoration: underline;
}

/* COLOR THEME PICKER */
.colorThemePicker{
  position: fixed;
  top: 70px;
  right: 20px;
  padding: 2px;
  border: 2px solid var(--dark-bg);
  border-radius: 3px;
  margin-right: -180px;
  transition: margin-right .7s;
  background-color: white;
}
.colorThemePicker.active{
  margin-right: 0px;
}
.colorThemePicker button{
  width: 22px;
  height: 22px;
  margin: 4px;
  border: none;
  border-radius: 15px;
  cursor: pointer;
}
#redThemePicker{
  background-color: var(--red-color);
}
#orangeThemePicker{
  background-color: var(--orange-color);
}
#greenThemePicker{
  background-color: var(--green-color);
}
#blueThemePicker{
  background-color: var(--blue-color);
}
#pinkThemePicker{
  background-color: var(--pink-color);
}

/* DARK MODE */
.dark {
  background-color: var(--dark-bg);
}
.dark .searchBar input{
  background-color: var(--dark-bg-alt);
  color: white;
  border: 1px solid white;
}
.dark .searchBar input::placeholder{
  color: rgb(160, 160, 160);
}
.dark .noteListToggler button{
  background-color: var(--dark-bg-alt);
  color: white;
}
.dark .noteContainer{
  color: white;
}
.dark .noteItem{
  background-color: var(--dark-bg-alt);
}
.dark .noteItem button{
  background-color: var(--dark-bg-alt);
  color: white;
}
.dark .noteItem:hover{
  background-color: rgb(25, 25, 25);
}
.dark .noteItem:hover button{
  background-color: rgb(25, 25, 25);
}
.dark .CRUDNote{
  color: white;
}
.dark .CRUDNote input, .dark .CRUDNote textarea{
  color: white;
  background-color: rgb(25, 25, 25);
  outline: none;
  border: 1px solid gray;
}
.dark .LoginRegister h2, .dark .LoginRegister form p, .dark .LoginRegister form p a{
  color: white;
}
.dark .LoginRegister form input{
  color: white;
  background-color: rgb(25, 25, 25);
}
.dark .colorThemePicker{
  background-color: var(--dark-bg);
  border: 2px solid whitesmoke;
}

/* RED THEME */
.red header{
  background-color: var(--red-color);
}
.red header button{
