/* MEDIA QUERY */
@media only screen and (max-width: 1150px) {
  .searchBar input{
    width: 450px;
  }
  .noteContainer{
    width: 840px;
  }
  .noteItem{
    width: calc(33% - 15px);
  }
  .CRUDNote{
    width: 840px;
  }
}
@media only screen and (max-width: 890px) {
  header h1{
    font-size: 1.5em;
  }
  h2{
    font-size: 1.3em;
  }
  .searchBar input{
    width: 400px;
  }
  .noteContainer{
    width: 580px;
  }
  .noteItem{
    width: calc(50% - 15px);
  }
  .CRUDNote{
    width: 580px;
  }
  .CRUDNote label{
    padding: 10px 0;
  }
  .CRUDNote label, .CRUDNote input, .CRUDNote p, .CRUDNote textarea{
    width: 100%;
    display: block;
    margin: 0;
  }
  .CRUDNote p{
    padding: 5px 10px;
    text-decoration: underline;
  }
  .CRUDNote .remainingChar{
    padding-top: 10px;
    text-decoration: none;
  }
  .CRUDNote.error p{
    text-decoration: none;
  }
}
@media only screen and (max-width: 630px) {
  header h1{
    display: none;
  }
  header nav{
    margin-left: auto;
  }
  .authed header nav{
    margin-left: 0;
    width: 100%;
  }
  .authed header ul li:first-child{
    margin-left: 0;
  }
  .authed header ul li:last-child{
    margin-left: auto;
  }
  .authed .colorThemePicker{
    left: -180px;
    right: auto;
    transition: margin-left .7s;
  }
  .authed .colorThemePicker.active{
    margin-left: 200px;
  }
  h2{
    font-size: 1.3em;
  }
  .searchBar input{
    width: 320px;
  }
  .noteContainer{
    width: 320px;
  }
  .noteListToggler button{
    font-size: .9em;
    margin: 3px;
    padding: 5px;
  }
  .noteItem{
    width: 100%;
    margin-right: 0;
  }
  .CRUDNote{
    width: 440px;
  }
}
@media only screen and (max-width: 450px) {
  footer{
    font-size: .8em;
  }
  .CRUDNote{
    width: 320px;
  }
  .LoginRegister{
    width: 320px;
  }
}
@media only screen and (max-width: 325px) {
  .searchBar input{
    width: 290px;
  }
  .noteContainer{
    width: 290px;
  }
  footer{
    font-size: .8em;
  }
  .CRUDNote{
